<template>
    <div style="margin-left:1%">
        
        <div style="height:10px"></div>
        <div>题目描述：{{ challenge.description }}</div>
        <div style="height:5px"></div>
	解题人数：<el-button type="text" @click="showSolvedUsers(challenge.qid)">{{ challenge.solvecount }}</el-button>
	<div style="height:5px"></div>
        <a :href="challenge.url">
            <el-button type="text">题目链接</el-button>
        </a>
    </div>
</template>

<script>
    import Challenge from '@/api/Challenge'
    export default {
        props: ['challenge'],
        data() {
            return {

            }
        },
        methods: {
            showSolvedUsers(id) {
                this.$emit('show', id)
            }
        },
        mounted() {

        },
    }
</script>



